html,body {
  position: relative;
  margin: 0;
  height: 100%;
  min-height: 100%;
}

.imagen {
  background-image: url(../imagen/Foto.png);
  height: 100vh;
  background-size: cover;
  opacity: 0.8;
  background-position: center;
  position: relative;
}
.imagen::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #000000;
  opacity: 0.4;
}

.title {
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border: 5px solid;
}

.title h1 {
  color: green;
  font-size: 70px;
}

.title p {
  text-align: center;
  font-size: 50px;
  color: #ffffff;
}

.lista{
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.skill{
  padding: 10px;
}

.skill > .fab{
  font-size: 3.5em;
  color: orange;
}


@media (max-width: 500px) {
  .title h1{
    font-size: 40px;
  }
  .title p{
    font-size: 30px;
  }
  .titulo1 p{
     margin:0 auto
     }
     .redes-footer{
      margin: 0 auto
      }
    }
