nav {
   width: 100%;
    z-index: 999;
    
  }
  
  nav ul {
    list-style-type: none;
    overflow: hidden;
    text-align: center;
    background-color: black;
    font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  }
  
  nav ul li {
    display: inline-block;
  }
  
  nav ul li a {
    display: inline-block;
    color: white;
    padding: 25px;
    text-decoration: none;
  }