.parrafo {
  text-align: center;
  padding: 0px 8px;
}

.parrafo h1 {
  font-size: 40px;
  margin-top: 40px;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
}

.container {
  display: flex;
  flex-direction: column;
  text-align: center;
  background-color: #c3c3c3;
}

.imagen1 {
  width: 300px;
  height: 300px;
  border-radius: 150px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 40px;
}

.texto {
  position: relative;
  text-align: center;
}

.texto2 {
  font-size: 20px;
  color: black;
  text-align: justify;
  margin: 0 auto;
  width: 45%;
}

.texto3 {
  text-align: center;
  width: 70%;
  padding-bottom: 20px;
  font-size: 30px;
  border-bottom: 2px solid #ffffff;
  margin: 10px auto 40px;
}

.descargar {
  text-align: center;
  margin-top: 40px;
  margin-bottom: 20px;
}

.descargar a {
  text-decoration: none;
  font-size: 20px;
  color: #0000FF;
}

.descargar i {
  padding: 10px;
}

@media (max-width: 1600px) {
  .descargar {
    margin-top: 20px;
  }
}

@media (min-width: 800px) and (max-width: 1200px) {
  .descargar {
    margin-top: 20px;
    margin-bottom: 0px;
  }
}

@media (max-width: 767px) {
  .parrafo {
    margin-top: 5px;
  }
  .descargar {
    margin-top: 20px;
    margin-bottom: 0px;
  }
}
