.titulo10{
    padding: 30px 0;
}

.titulo10 img{
    width: 10%;
    margin: auto;
    display: block;
}

.titulo10 h1{
    margin-top: -50px;
    margin-bottom: 40px;
    text-align: center;
    color: black;
    font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
}

.formulario {
    margin-top: 4px;
    color: #F7F9F9;
   padding:60px;
  
}

.formulario form{
    background-color: rgba(0, 0, 0, 0.8);
    border-radius: 10px;
    color: #F7F9F9;
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-content: center;
    padding: 10px;
    font-size: 26px;
    width: 40%;
    text-align: center;
    margin: 0 auto;
}

.formulario form .Block{
grid-column: 1/3;
}

.formulario input{
    font-size: 18px;
    margin-top: 20px;
    padding: 5px;
}
.formulario label {
    margin: 10px 0;
}


.formulario textarea {
    border: none;
    border-radius: 5px;
    padding: 10px;
    margin-top: 5px;
    font-size: 18px;
    height: 260px;
}

.btn-send{
    border-radius: 10px;
    color: #F7F9F9;
    background-color: #5DADE2;
    border: none;
    width: 40%;
    margin: 0 auto;
    outline: none;
    grid-column: 1/3;
    font-size: 20px;
    transition: all .5s;
    cursor: pointer;
    border: 2px solid #F7F9F9;
}

/* mediaqueries*/
@media screen and (max-width:1200px){
    .titulo10 img{
      padding-left: 105px;
    }
    .formulario{
        width: 100%;
    }
    .formulario input{
        padding: 0px;
    }
}



@media screen and (max-width:400px){
    .formulario input{
    font-size: 12px;
    }
    
    .formulario textarea{
        max-width: 400px;
    }
    }
    
    